.statistics {
  box-shadow: 1px 1px 5px 1px rgb(8, 8, 8);
  padding-top: 10px;
}

.title {
  text-align: center;
  font-size: 30px;
  text-transform: uppercase;
  padding: 0 15px;
  margin-bottom: 30px;
  color: grey;
}

.statList {
  display: flex;
  list-style: none;
  padding: 0;
}

.item {
  width: calc(100% / 4);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 10px;
}

.label {
  margin-bottom: 10px;
}

.percentage {
  font-size: 15px;
}
